import Popup from "components/Popup";
import React, { useState } from "react";
import styled from "styled-components";

const CancelButton = styled.button`
  background-color: ${(props) => props.theme.buttonGray};
  color: white;
  border: none;
  font-size: 15px;
  padding: 15px 30px;
  border-radius: 10px;
  margin-top: 40px;
  margin: 10px;
  width: 210px;

  :hover {
    background-color: ${(props) => props.theme.boxGray};
    cursor: pointer;
  }

  @media only screen and (max-width: 640px) {
    width: 35vw;
    height: 10vw;
    font-size: 3.2vw;
  }
`;

const ConfirmButton = styled.button`
  background-color: ${(props) => props.theme.mainPink};
  color: white;
  border: none;
  font-size: 15px;
  padding: 15px 30px;
  border-radius: 10px;
  margin-top: 40px;
  margin: 10px;
  width: 210px;

  :hover {
    background-color: ${(props) => props.theme.mainPinkHover};
    cursor: pointer;
  }

  @media only screen and (max-width: 640px) {
    width: 35vw;
    height: 10vw;
    font-size: 3.2vw;
  }
`;

const PopupTitle = styled.h3`
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  line-height: 20px;
  margin-bottom: 10px;
  @media screen and (max-width: 640px) {
    font-size: 5.3vw;
    line-height: 6vw;
    margin-bottom: 5vw;
  }
`;

const PopupDescription = styled.p`
  font-weight: 500;
  font-size: 13px;
  line-height: 18px;
  text-align: center;
  margin-bottom: 10px;
  @media screen and (max-width: 640px) {
    font-size: 3.5vw;
    line-height: 4.5vw;
    margin-bottom: 5vw;
  }
`;

const ConfirmModal = ({
  setModal,
  userInfo,
  updateUserInfo,
  totalPrice,
  setHasBought,
}) => {
  const [complete, setComplete] = useState(false);
  return (
    <Popup padding={[30, 30]}>
      {complete ? (
        <>
          <PopupTitle> !</PopupTitle>
          <div className="buttonsContainer">
            <button
              onClick={() => {
                setModal(false);
              }}
            >
               
            </button>
            <button
              onClick={() => {
                setModal(false);
                setTimeout(() => {
                  const formContainer =
                    document.getElementById("formContainer");
                  formContainer.scrollIntoView({ behavior: "smooth" });
                }, 500);
              }}
            >
               
            </button>
          </div>
        </>
      ) : (
        <>
          <PopupTitle> </PopupTitle>
          <PopupDescription>
                ,
            <br />
               .
          </PopupDescription>
          <div className="buttonsContainer">
            <button
              onClick={() => {
                setModal(false);
              }}
            >
              
            </button>
            <button
              onClick={() => {
                const res = updateUserInfo({
                  balance: userInfo.balance - totalPrice,
                });
                if (res === 200) {
                  setComplete(true);
                  setHasBought(true);
                } else if (res === 400) {
                  setModal(false);
                }
              }}
            >
               
            </button>
          </div>
        </>
      )}
    </Popup>
  );
};

export default ConfirmModal;
